.product-product {
  .thumbnail {
    border: none;
    border-radius: 0; }
  h1, .product-price {
    text-transform: none; }
  .product-images {
    .thumbnail-main {
      margin-bottom: 3px; }
    &_additional {
      overflow: hidden;
      .thumbnail {
        float: left;
        max-width: 115px;
        margin-right: 3px;
        &:last-of-type {
          margin-right: 0; } } } }
  .review-link {
    font-size: rem(13);
    margin-left: rem(8); }

  .product-price {
    &-block {
      font-size: fz(35px); } }

  .product {
    &_brand {
      font-weight: $fontBold;
      font-size: fz(20px);
      margin-bottom: 30px; }
    &-quantity {
      margin-bottom: 55px; }
    &-rating {
      @include flex;
      align-items: center;
      margin-bottom: 40px;
      .rating {
        margin-right: 60px; } }
    &-description {
      margin-top: rem(175px); } }

  .attributes-group {
    margin-bottom: rem(25); }
  .product-rating {
    margin-bottom: rem(45); } }

.product_brand {
  margin: rem(30) 0; }

.product-price {
  margin-bottom: rem(20);
  &-block {
    @include flex;
    font-weight: $fontBold; }
  &_actual {
    color: $p_choral;
    font-weight: $fontBold;
    margin-right: 35px; }
  &_old {
    font-weight: $fontBold;
    text-decoration: line-through; } }

.discount {
  margin-bottom: rem(35); }

#product {
  @include mq('phablet') {
    .product-quantity {
      ~ .d-flex {
        flex-wrap: wrap; } }
    #button-cart, .wish {
      width: 100%;
      flex: 0 0 100%;
      margin-left: 0 !important;
      margin-top: rem(20); } }
  .wish {
    margin-left: rem(23); } }

#product-product {
  ~ .down-side {
    margin-top: 9rem;
    @include mq('phablet') {
      margin-top: 4rem; } } }
