// login page
.login-page {
  a.password-forgot {
    color: $p_black;
    margin-top: rem(20);
    display: block;
    text-decoration: underline;
    @include transition;
    &:hover {
      color: $p_choral; } }
  a.btn {
    align-items: center;
    display: flex;
    margin-top: rem(50); } }
