.breadcrumb {
  background: none;
  padding: 0;
  font-size: fz(13px);
  margin-top: rem(30px);
  margin-bottom: rem(68px);
  @include mq('phablet') {
    margin-bottom: rem(10px); }
  @include border-radius(0);
  > li + li:before {
    color: $p-black; }
  a {
    color: $p_black;
    @include transition;
    &:hover {
      color: $colorMain; } } }
