.main-nav {
  .navbar {
    margin-bottom: 0 !important; }
  .mega-menu {
    width: 100%;
    padding: 0;
    .menu {
      border: none;
      &-item {
        border: none;
        .sub-menu {
          width: $containerWidth;
          @include box-shadow('none');
          padding: 60px 63px; } } }
    a {
      color: $p_white;
      padding: rem(17px) 0;
      @include transition;
      &:hover {
        background: none;
        color: #eb7363; } } }
  li {
    margin-right: 58px;
    &:last-of-type {
      margin-right: 0; }
    > a {
      color: $p_white;
      padding: rem(17px) 0;
      @include transition;
      &:hover,&:active {
        background: none;
        color: #eb7363; } } }
  .work-time {
    line-height: 1; }
  .customer-menu {
    a {
      color: #000; } } }

#top {
  background: none;
  border-bottom: none; }

.vd.vd {
  .navbar-collapse {
    padding-left: 0;
    padding-right: 0; } }

#menu .open > a,
#menu .dropdown-menu {
  background-color: $colorMain;
  color: $p_white; }

#menu .dropdown-menu {
  z-index: z('menu');
  padding: rem(10);
  li {
    margin: rem(10) 0; } }
