@keyframes Pulse {
  0% {
    transform: scale(1); }
  50% {
    transform: scale(1.2); }
  100% {
    transform: scale(1); } }

@keyframes PulseFadeOut {
  0% {
    transform: scale(1);
    opacity: 1; }
  50% {
    transform: scale(1.2);
    opacity: 0; }
  100% {
    transform: scale(1);
    opacity: 1; } }

@keyframes Slide {
  0% {
    right: -9999px; }

  50% {
    right: 40px; }

  100% {
    right: 20px; } }

@keyframes SlideOut {
  0% {
    right: 20px; }

  50% {
    right: 40px; }

  100% {
    right: -9999px; } }

@keyframes Writing {
  0% {
    @include prefixes("transform", rotate(-15deg)); }
  50% {
    @include prefixes("transform", rotate(-35deg)); }
  100% {
    @include prefixes("transform", rotate(-15deg)); } }
