%jag_input {
  padding: rem(5) rem(20);
  height: rem(55);
  outline: none;
  margin-top: 0;
  font-size: $fontSizeInput;
  @include border-radius(0);
  @include transition();
  &:not(.btn) {
    border: $borderMain;
    background: #fff;
  }
  &:focus {
    @include box-shadow('none');
    border: $borderMain;
    border-color: $borderColorFocus;
    box-shadow: none;
    background-color: $FormBackFocus;
  }
  &.active {
    background: $FormBackActive;
    border-color: $borderColorActive;
  }
}

.d-vis {
  &.form-group {
    //shame for bootsrap make margins
    margin-left: -0px;
    margin-right: -0px;
    margin-bottom: rem(33); // todo paste to config
    label {
      font-weight: normal;
      font-size: rem(12);
      margin-bottom: rem(8);
      padding: 0;

    }
    &.has-error {
      .form-control {
        border: $borderMain;
        border-color: $borderColorError;
        background: $FormBackError;
      }
      span, label, .text-danger {
        font-size: rem(12);
        color: $p_red;
      }
      .text-danger {
        margin-top: rem(8);
      }
    }
    &.required {
      label:after {
        content: '*';
        .product-product & {
          content: '';
        }
      }
    }
    .form-horizontal .form-group {
      margin: 0;
      margin-bottom: rem(35);
    }
    &.row {
      align-items: center;
      justify-content: flex-start;
      > .col-sm-2, > .col-sm-10 {
        //max-width: initial ;
        //flex: 1 0;
      }
    }
  }
  &.form-control {
    @extend %jag_input;
  }
}

