.products-list {
  position: relative;
  //margin-top: -15px
  z-index: z('product-thumb');
  .product-thumb_body {
    width: 100%; } } // fix bug on small btn in thumb

.thumb-box {
  position: relative;
  margin-bottom: 50px;
  min-height: 410px;
  @include mq('desktop') {
    min-height: 360px; }
  @include mq('laptop') {
    min-height: 341px; }
  @include mq('tablet-wide') {
    min-height: 305px; }
  @include mq('tablet') {
    min-height: 245px; }
  @include mq('tablet-small') {
    min-height: 380px; }
  @include mq('phone') {
    min-height: 400px; }
  &:hover {
    .product-thumb {
      padding: rem(15);
      height: auto;
      @include box-shadow('0 3px 43px 0 rgba(0, 0, 0, 0.16)');
      @include prefixes('transform', 'scale(1.1)');
      z-index: z('product-thumb');
      .rating,.product-options_color {
        visibility: visible;
        opacity: 1;
        display: block; }
      .btn-buy {
        @extend %jag_btn;
        visibility: visible;
        opacity: 1;
        .product-list & {
          margin-left: 0; } } } } }

.product-thumb.vis-product-thumb {
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  padding: 15px;
  //margin: -15px
  width: 100%;
  background: #fff;
  @include transition('transform',.2s);
  .btn {
    margin: auto; }
  .img-responsive {
    max-width: 100%;
    height: auto; }
  @include prefixes('transform', 'scale(1)');
  .product-thumb_image {
    position: relative;
    margin-bottom: rem(15);
    //border: 1px solid $p_grey_product_thumb
    display: flex;
    align-items: center;
    justify-content: center;
    .col-lg-4 & {
      min-height: 325px;
      @include mq('laptop') {
        min-height: 255px; }
      @include mq('tablet') {
        min-height: 150px; }
      @include mq('tablet-small') {
        min-height: 290px; }
      @include mq('phone') {
        min-height: 330px; } }
    img {
      margin: auto; } }
  .rating {
    $symbolSize: 10px;
    overflow: hidden;
    visibility: hidden;
    opacity: 0;
    display: none; }
  .product-name {
    display: block;
    font-size: fz(16px);
    min-height: 40px;
    margin-bottom: 11px;
    a {
      color: #000; }
    + p {
      height: 70px; } }
  .product-description {
    font-size: fz(13px);
    display: none; }
  .product_brand {
    display: block;
    font-size: fz(13px);
    margin-bottom: 8px; }
  .product-price-block {
    margin-bottom: 5px; }
  .product-price {
    font-size: fz(16px);
    margin-right: 14px;
    &:last-of-type {
      margin-right: 0; } }

  .badge {
    top: 12px;
    left: 12px; }
  .additional-info {
    margin-bottom: 20px;
    @include flex;
    justify-content: space-between;
    align-items: flex-end; }
  .product-options {
    font-size: fz(13px);
    &_color {
      overflow: hidden;
      opacity: 0;
      line-height: 1; }
    &_size {
      color: #949494; }
    color: #656565; }
  .btn-buy {
    visibility: hidden;
    opacity: 0;
    max-height: 100px;
    height: 0;
    order: 3;
    @include transition;
    .product-grid & {
      width: 100%; } } }


//
.product-list {
  .product-thumb {
    @include flex;
    margin-bottom: $indentBase;
    padding: 24px 24px 33px;
    &_image {
      margin-right: $indentBase;
      margin-bottom: 0;
      flex-shrink: 0; }
    &_body {
      @include flex;
      flex-direction: column;
      align-items: flex-start;
      padding-top: 25px; }
    .product-name {
      font-size: fz(25px);
      order: 0; }
    .rating {
      display: block;
      visibility: visible;
      opacity: 1;
      margin-bottom: 15px;
      order: 1; }
    .product_brand {
      order: 2; }
    .product-description {
      display: block;
      order: 3;
      margin-bottom: 20px; }
    .product-price-block {
      order: 2; } } }
//
.product-grid {
  .product-thumb {
    &_image {
 }      //height: 337px
    .additional-info {
      float: left; }
    .rating {
      float: right;
      margin-top: rem(20);
      margin-bottom: rem(20); } } }

[data-is='vd-layout-main'] {
  .product-layout-sm {
    height: rem(345);
    padding-left: 0;
    padding-right: 0;
    .product-name {
      font-size: fz(15px); } }
  .product-layout {
    min-height: rem(510);
    padding: 0;
    &.product-list {
      height: 331px;
      .product-options_size,
      .product-options_color {
        opacity: 1;
        visibility: visible; }
      .additional-info {
        @include flex;
        flex-direction: column-reverse;
        align-items: flex-start;
        order: 2; }
      .btn {
        width: auto; }
      .product_brand {
        font-size: fz(20px); }
      .product-price-block,
      .product-price {
        font-size: fz(25px); } } } }

.product-thumb .add-to-fave {
  position: absolute; }

.vd-product-thumb {
  overflow: visible;
  border: none;
  margin-bottom: 0; }
