.options-wrap {
  .form-group {
    margin-bottom: rem(50); }
  label.control-label {
    @extend .like-heading-h3;
    margin-bottom: rem(14); } }
.custom-control {
  cursor: pointer;
  transition: all .3s ease;
  box-sizing: content-box; }


.options {
  display: flex; }


.advanced_options input[type=radio],.advanced_options input[type=checkbox] {
  position: absolute;
  opacity: 0;
  cursor: pointer; }

.options--color {
  font-size: 0;
  label {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 50px;
    margin-right: 7px;
    cursor: pointer;
    &:last-of-type {
      margin-right: 0; }
    img {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1; } }

  .product-color {
    width: 44px;
    height: 44px;
    margin: 0;
    border: 3px solid;
    &--red {
      // background: #962512
      border-color: #962512;
      box-shadow: inset 0 0 0 3px #962512; }
    &--brown {
      // background: #735b47
      border-color: #735b47;
      box-shadow: inset 0 0 0 3px #735b47; }
    &--blue {
      // background: #324767
      border-color: #324767;
      box-shadow: inset 0 0 0 3px #324767; }
    &--black {
      // background: #000
      border-color: #000;
      box-shadow: inset 0 0 0 3px #000; } } }

.options {
  input[type=radio], input[type=checkbox] {
    &:checked {
      + .product-color {
        box-shadow: inset 0 0 0 3px #fff;
        &--red {
          border-color: #962512;
          background: darken(saturate(#bd6356, 22), 19); }
        &--brown {
          border-color: #735b47;
          background: darken(saturate(#735b47, 22), 19); }
        &--blue {
          border-color: #324767;
          background: darken(saturate(#324767, 22), 19); }
        &--black {
          border-color: #000;
          background: darken(saturate(#000, 22), 19); } }
      ~ img {
        box-shadow: inset 0 0 0 3px $p_choral; } } } }
.options--size {
  .custom-control {
    width: 54px;
    height: 44px;
    border: 1px solid #cbcfd8;
    .size-value {
      display: block;
      width: 100%;
      height: 100%;
      text-align: center;
      line-height: 44px; } }

  label {
    margin-right: 14px;
    img {
      display: none; }
    input[type=radio] {
      position: absolute;
      opacity: 0;
      &:checked {
        + .custom-control {
          border-color: #bd6356;
          box-shadow: inset 0 0 0 2px #bd6356; }
        ~ img {
          box-shadow: inset 0 0 0 3px $p_choral; } }
      &[disabled] {
        + .custom-control {
          position: relative;
          border-color: #dedede;
          color: #dedede;
          cursor: not-allowed;
          &:after {
            content: '';
            position: absolute;
            display: inline-block;
            left: 26px;
            top: -13px;
            width: 1px;
            height: 71px;
            background: #dedede;
            transform: rotate(51deg); } } } } } }

.product-option-wrap {
  display: flex;
  align-items: center;
  margin-right: rem(5); }
