.d_product_sort {
  margin-bottom: 2.175rem;
  .buttons-view {
    .btn {
      background: none;
      padding: 0;
      width: 17px;
      height: 17px;
      line-height: 1;
      margin-right: 5px;
      outline: none;
      //&:last-of-type
      //  margin-right: 10px
      &.active {
        @include box-shadow('none');
        .fas {
          color: $p_choral; } } }
    .fas {
      font-size: fz(17px);
      color: #cbcfd8; } }
  .col {
    display: inline-flex;
    align-items: center;
    &-view-type, &.col-view-type .btn {
      padding: 0; }
    &-sort {
      margin-right: 43px;
      @include mq('phablet') {
        margin-right: 0px; } }
    &-sort {
      .field-label {
        margin-right: 35px;
        margin-bottom: 0; } }
    &-wishlist {
      text-align: right;
      margin-right: 54px;
      @include mq('phablet') {
        margin-right: 10px;
        margin-bottom: 10px;
        width: 100%; } } }
  .sort-options {
    display: flex;
    justify-content: flex-end;
    font-size: rem(15);
    @include mq('phablet') {
      flex-wrap: wrap; }

    a {
      color: $p_black;
      width: 100%;
      &:hover {
        text-decoration: underline !important; } }
    label {
      font-weight: normal; } }
