#cart {
  &.open {
    .dropdown-toggle {
      box-shadow: none;
      text-decoration: underline; } }
  .dropdown-toggle {
    background-color: transparent;
    font-size: rem(15);
    cursor: pointer;
    &:focus,&:active {
      box-shadow: none;
      text-decoration: underline; } }
  .dropdown-menu {
    &:after {
 }      //+transition('all', 1s)
    &.backdrop {
 }      //&:after
    min-width: rem(400);
    right: 0;
    padding: rem(25);
    border-radius: 0;
    box-shadow: none;
    p {
      margin-bottom: 0; }
    .table {
      td {
        padding-bottom: rem(30);
        border: none; }
      .total {
        padding-right: rem(20); }
      a {
        color: $colorMain; } }
    .img-thumbnail {
      min-width: rem(90);
      border-radius: 0;
      border: none; }
    .product-quantity-wrap {
      margin-top: rem(20); }
    .table-total {
      margin-bottom: rem(5);
      tr:last-child {
        border-top: $defaultBorder;
        td {
          padding-top: rem(15); } }
      td {
        padding-right: 20px; }
      tr:not(:last-child) td {
        padding-top: rem(5);
        padding-bottom: rem(12); } }
    .checkout-cart-btn-wrap {
      .btn:first-child {
        margin-right: rem(10); } }
    [class*="la-"] {
      color: $p_choral;
      @include center($width-loader,$height-loader); } }
  .dropdown-backdrop {
    background-color: #000;
    opacity: .5; } }
