.form-horizontal .checkbox{
}
.d-vis {
  //hide default checkbox
  &.checkbox{
    //botstrap over ride :\
    padding-top: 0;
    min-height: $checkbox-height !important;
    display: inline-block;
  }
  .checkbox-text {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: $checkbox-width;
    height: $checkbox-height;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      display: inline-block;
      width: 100%;
      height: 100%;
      border-radius: 1px;
      border: $borderMain;
      background: $p_white;
    }
  }
  & input[type="checkbox"] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    margin: 4px; //don't change it's afect on size checkbox
    &:checked + .checkbox-text:before{
      content: '\f00c';
      font-family: 'Font Awesome 5 Pro';
      font-size:rem(12);
      text-align: center;
      font-weight: normal;
      color: $p_blue_dark;
    }
    &:hover + .checkbox__text:before  {
      background-color: $p_grey_currency;
    }
    &:focus + .checkbox__text:before {
      border: 1px solid $p_blue_light;
    }

  }
  & label.checkbox {
    display: inline-block;
  }

}

