select.d-vis {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;       /* remove default arrow */
  cursor: pointer;
  #product-category &{
    min-height: initial;
    height: initial; /* Clear iniput height*/
    border: none;
    box-shadow: none;
  }
  &:focus,&:active{
    outline: none
  }


  //background-image: url(...);   /* add custom arrow */
}
//thats mine
.select-container {
  position: relative;
  &:after {
    content: '\f107';
    @include vcenter();
    font-size: 23px;
    font-family: 'Font Awesome 5 Pro'; /*todo add support Font free*/
    color: $p_grey_dropdwn;
    font-weight: $fontLight;
    right: 12px;
    pointer-events: none;
  }
}
