@import "base/palette";
/**
 * Color
 */
$colorInfo: $p_old_gold;
$colorSuccess: $p_aqua_forest;
$colorDanger: $p_red;

// primary
$p_picton-blue: #4fb5e7;
$p_picton-blue-darken: #27b7fd;
$p_picton-blue-darkenx2: #1589c1;
// success
$p_de-york: #86ce99;
$p_fruit-salad: #2cbd53;
$p_fruit-salad-darken: #4faf68;
// danger
$p_chestnut-rose: #cf4949;
$p_chestnut-rose-darken: #da3d3d;
$p_chestnut-rose-darkenx2: #bc3a3a;
// warning
$p_tulip-tree: #f0b840;
$p_tulip-tree-darken: #eba612;
$p_tulip-tree-darkenx2: #d59202;
// default
$p_alto: #cfcfcf;
$p_alto-darken: #b1b1b1;
$p_alto-darkenx2: #959595;
// info
$p_snuff: #d9dcec;
$p_snuff-darken: #c6cce4;
$p_snuff_darkenx2: #abb2d8;

// main
$colorMain: $p_black;
$colorSecondary: $p_shark;
$colorMainLighten: lighten($colorMain, 3.3);
$colorMainDarken: darken($colorMain, 5.9);

// Typography
$colorLink: $p_lochmara;
$colorHeading: $colorSecondary;

// Bg
$colorBgMain: $p_athens-gray;
$colorBgSecondary: #000;

// Button
// main
$colorMainButton: #b95547;
$colorMainButtonHover: #953427;
$colorMainButtonActive: #7d2e21;

$buttonHeight: rem(50);
// secondary (success)
$colorSecondaryButton: #2e4870;
$colorSecondaryButtonHover: #15315b;
$colorSecondaryButtonActive: darken(#15315b, 6);
// danger
$colorDangerButton: $p_chestnut-rose;
$colorDangerButtonHover: $p_chestnut-rose-darken;
$colorDangerButtonActive: $p_chestnut-rose-darkenx2;
// warning
$colorWarningButton: $p_tulip-tree;
$colorWarningButtonHover: $p_tulip-tree-darken;
$colorWarningButtonActive: $p_tulip-tree-darkenx2;
// default
$colorDefaultButton: #d9dce1;
$colorDefaultButtonHover: #b4b8bf;
$colorDefaultButtonActive: #8e9298;
// info
$colorInfoButton: $p_snuff;
$colorInfoButtonHover: $p_snuff-darken;
$colorInfoButtonActive: $p_snuff-darkenx2;

// Alert
// info
$colorNotifBg-info: $p_pearl_lusta;
$colorNotifBorder-info: $p_chamois;
$colorNotifFont-info: $colorInfo;
// success
$colorNotifBg-success: $p_panache;
$colorNotifBorder-success: $p_fringy_flower;
$colorNotifFont-success: $colorSuccess;
// danger
$colorNotifBg-danger: $p_chablis;
$colorNotifBorder-danger: $p_vanilla_ice;
$colorNotifFont-danger: $colorDanger;

// border
$borderColor: darken($p_blue_light, 3);
$borderColorFocus: #7a93cc;
$borderColorActive: #7accb0;
$borderColorError: $p_red;
$defaultBorder: 1px solid $borderColor;
/**
 * Font
 */
// main
$fontMain: 'Montserrat';
$fontSecondary: 'Blogger Sans';
$fontSecondaryBold: 'Blogger Sans Bold';
@include font-face($fontSecondary, '  ../../../../font/skin/jag/blogger-sans/BloggerSans-Bold');
$line-height-base: 1;
// font color
$fontColorMain: #000;
$fontColorSecondary: $p_waterloo;

// weight
$fontExLight: 100;
$fontLight: 300;
$fontNormal: 400;
$fontMedium: 500;
$fontBold: 700;
$fontBlack: 900;

// font size
$fontSizeBase: 16px;
$fontSize-xs: fz(13);
$fontSize-sm: fz(15);
$fontSize-md: fz(20);
$fontSize-lg: fz(26);
$fontSizeH1: fz(35);
$fontH1Spacing: 10px;

$fontSizeH2: fz(25);
$fontH2Spacing: 5px;

$fontSizeH3: fz(20);
$fontH3Spacing: 10px;

$fontSizeH4: fz(15);
$fontSizeH5: fz(13);

$fontSizeDate: $fontSize-xs;

$border-radius: 0;
$input-bg: rem(20);
//Forms
$FormBackFocus: #f9f9fc;
$FormBackActive: #f9fcfb;
$FormBackError: #fcf8f8;
$fontSizeInput: $fontSize-xs;
$custom-control-gutter: rem(10);
$custom-control-spacer-x: rem(5);
$custom-control-indicator-checked-color: $p_grey;
$custom-control-indicator-focus-box-shadow: darken($p_grey, 3);
$custom-control-indicator-active-color: $p_grey;
$custom-control-indicator-active-bg: $p_white;
$custom-control-label-disabled-color: $p_alto-darken;
$custom-control-indicator-disabled-bg: darken($p_white ,3);
$custom-control-indicator-size: rem(20);
$custom-control-indicator-bg: $p_white;
//checkbox
$checkbox-width: rem(20);
$checkbox-height: rem(20);
//radio
$radio-width: rem(20);
$radio-height: rem(20);
$radioBorderColor: $p_blue_light;
$radioInnerColor: $p_blue_dark;
$widhtControl: rem(20);
$heightControl: rem(20);
/**
 * Indent
 */
// outer
$indentBase: rem(30px);

$marginVerticalBase: $indentBase;

// inner
$paddingVerticalButton: 12px;
$paddingVerticalButton-md: 6px;
$paddingVerticalButton-sm: 0;

$paddingHorizontalButton: 77px;
$paddingHorizontalButton-md: 38px;
$paddingHorizontalButton-sm: 15px;

/**
 * Border
 */
$borderRadiusBase: 4px;
$borderMain: 1px solid $borderColor;

/**
 * Field
 */
$fieldHeightBase: rem(55);
$fieldPaddingVertical: 12px;
$fieldPaddingHorizontal: 20px;

// line height
$lineHeightBase: 1.5;
$lineHeightP: lh(22, $fontSizeBase);
$lineHeightButton: $buttonHeight;

/**
 * Cart
 */
$cartIconMark: $p_burnt_sienna;

/**
 * Grid
 */
$containerWidth: 1470px;

// rating
$ratingFilled: $p_choral;
$ratingEmpty: #bdc0c4;

//pagination
$pagination-color: $colorMain;
$pagination-bg: $p_white;
$pagination-border: $p_blue_light;
$pagination-active-color: $p_white;
$pagination-active-bg: $p_choral;
$pagination-active-border: $p_choral;

//loader
$width-loader: 32px;
$height-loader: $width-loader;
