.product-quantity {
  @include flex;
  .quantity-control {
    border: 1px solid #cbcfd8;
    width: 56px;
    height: 46px;
    text-align: center;
    padding: 0 10px;
    line-height: 45px;
    i {
      line-height: inherit; }
    &.decrement,
    &.increment {
      cursor: pointer;
      @include transition;
      &:hover {
        background: #cbcfd8; } }
    &.field {
      position: relative;
      margin: 0 -1px;
      @include prefixes('appearance', 'none'); } } }


input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  @include prefixes('appearance', 'none'); }
