.newsletter-section {
  &_heading p:first-of-type {
    text-transform: uppercase; }

  [data-is="vd-block-mailchimp"] .vd-mailchimp .form-inline {
    @include flex;
    .form-group {
      margin: 0; }
    .form-control {
      @include placeholder {
        color: #fff !important; } }
    .vd-mailchimp-submit {
      text-transform: uppercase;
      font-weight: $fontBold;
      @include transition; } } }
