%jag_btn {
  max-width: $paddingHorizontalButton*2 + 100px;
  display: block;
  width: 100%;
  height: $fieldHeightBase;
  vertical-align: middle;
  padding-top: rem(15);
  padding-bottom: rem(15);
  @include transition;
  &:focus, &:active, &:active:hover, &:active:focus {
    @include prefixes('box-shadow', 'none');
    outline: none; } }

//.btn-vis
.d-vis {
  &.btn:not(.btn-link,.btn-inverse) {
    font-size: fz(14px);
    text-transform: uppercase;
    font-weight: $fontBold; }
  &.btn {
    color: $p_black;
    border-radius: unset;
    &-primary {
      @extend %jag_btn;
      color: $p_white;
      background: $colorMainButton;
      border: none;
      &:hover {
        background: $colorMainButtonHover;
        border-color: darken($colorMainButton, 12); } }
    &:focus, &:active, &:active:hover, &:active:focus {
        background: $colorMainButtonActive;
        outline: none; }

    &-secondary, &-success {
      @extend %jag_btn;
      color: $p_white;
      background: $colorSecondaryButton;
      &:hover {
        color: $p_white;
        background: $colorSecondaryButtonHover; }
      &:focus, &:active, &:active:hover, &:active:focus {
        background: $colorSecondaryButtonActive; } }

    &-danger {
      color: $p_white;
      background: $colorDangerButton;
      &:hover {
        background: $colorDangerButtonHover; }
      &:focus, &:active, &:active:hover, &:active:focus {
        background: $colorDangerButtonActive; } }
    &-warning, &-like-warning {
      color: $p_white;
      background: $colorWarningButton;
      &:hover {
        background: $colorWarningButtonHover; }
      &:focus, &:active, &:active:hover, &:active:focus {
        background: $colorWarningButtonActive; } }
    &-default,
    &[disabled] {
      @extend %jag_btn;
      color: $p_white;
      background: $colorDefaultButton;
      color: #000;
      &:hover {
        background: $colorDefaultButtonHover;
        color: #000; }
      &:focus, &:active, &:active:hover, &:active:focus {
        background: $colorDefaultButtonActive;
        color: $p_white; } }
    &-ghost {
      background: none;
      &:hover {
        background: none; }
      &:focus, &:active, &:active:hover, &:active:focus {
        background: none; } }
    &-info {
      background: $colorInfoButton;
      &:hover {
        background: $colorInfoButtonHover; }
      &:focus, &:active, &:active:hover, &:active:focus {
        background: $colorInfoButtonActive; } }
    &-shrink {
      padding: 0 rem(55);
      display: flex;
      align-items: center;
      justify-content: center; }
    &-inline {
      display: inline-block; }
    &:focus, &:active , &:active:focus {
      outline: none;
      box-shadow: none; }
    &-lg {
      min-width: $paddingHorizontalButton*2 + 100px; } } }
