//botstrap over ride :\
.form-horizontal .radio {
  padding-top: 0;
  min-height: $radio-height !important;
}

//remove side effect from required
.form-group.required label.radio:after {
  content: '';
}

.d-vis {
  //hide default radio
  .radio-text {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    span {
      //margin-left:40px;
    }
    &:before {
      content: '';
      position: relative;
      margin-right: rem(5);
      top: 0;
      display: inline-block;
      width: $radio-width;
      height: $radio-height;
      border-radius: 100%;
      border: $borderMain;
      background: $p_white;
    }
  }
  & input[type="radio"] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    margin: 4px; //don't change it's afect on size radio
    &:checked + .radio-text span {
      content: ' ';
      width: 6px;
      height: 6px;
      display: block;
      margin: auto;
      border-radius: 100%;
      background: $p_blue_dark;
      position: absolute;
      top: $radio-width - rem(13);
      left: 7px;
    }
    &:hover + .radio-text:before {
      background-color: $p_grey_currency;
    }
    &:focus + .radio-text:before {
      border: 1px solid $p_blue_light;
    }

  }
  & label.radio {
    display: inline-block;
  }

}

